body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height:100%;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background: darkgrey;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  border-radius: 8px;
  background: dimgrey;
}



